<template>
    <div class='worklist'>
        <div class="topInfo">
            <h2 class='title'>工作台</h2>
            <div class="searchSign">
              <input type="text" placeholder="输入姓名或手机号" v-model="userStr" class="userInput el-input__inner"/>
              <div class="search" @click="searchSign">搜索签到</div>
              <i class="iconfont icon-icon_delet" @click="clearUserstr" v-show="isHide"></i>
              <ul class="searchList">
                  <li class="searchItem" v-for="(item,index) in searchList" :key="index" @click="searchClick(item)">{{item.userName || item.email || item.phone}}</li>
              </ul>
            </div>
        </div>
        <Table  :tableData="tableData" :tableQuery="tableQuery"  :tabhide="tabhide" :tableKey="tableKey" ref="table" :total="total" :isPage = "isPage" :pageSize = "pageSize"  v-on:getQueryParam = "getQueryParam"
         :tableTitle="tableTitle" v-on:changeVal="changeParam" v-on:getRowdata="getRowdata"></Table>
    </div>
</template>

<script>
import Table from '../../components/Table'
import {courseAppointment, getbranchcoach, getallbranch, cancelAppoint, workspaceSearch} from '../../services/service.js'
import {isApiResponseOk} from '../../utils/com.js'
export default {
  data () {
    return {
      tableQuery: [
        {
          name: 'startTime',
          type: 'start_date',
          model: '',
          title: '选择开始日期',
          format: 'yyyy-MM-dd HH:mm:ss',
          text: '时间: ',
          class: 'startData'
        },
        {
          name: 'endTime',
          type: 'end_date',
          model: '',
          title: '选择结束日期',
          format: 'yyyy-MM-dd HH:mm:ss',
          text: '到',
          class: 'endData'
        },
        {
          name: 'studentInfo', 
          title: '输入姓名或手机号',
          type: 'input',
          model: '',
          text: '学员: '
        },
        {
          name: 'coachId',
          title: '请选择',
          type: 'select',
          model: '',
          text: '教练: ',
          selectArr: []
        },
        {
          name: 'courseType',
          title: '请选择',
          type: 'select',
          model: '',
          text: '课程类型: ',
          selectArr: []
        },
        {
          name: 'branchId',
          title: '请选择',
          type: 'select',
          model: '',
          text: '门店: ',
          selectArr: []
        }
      ],
      tableKey: [
        {name: '会员卡号', value: 'userVipCardCode', nor: true},
        {name: '学员姓名', value: 'studentName', nor: true},
        {name: '学员类别', value: 'vip', studentKind: true},
        {name: '联系方式', value: 'studentPhone', nor: true},
        {name: '预约教练', value: 'coachName', nor: true},
        {name: '预约课程种类名', value: 'courseName', nor: true},
        {name: '门店', value: 'branchOfficeName', nor: true},
        {name: '预约课程时间', value: 'startTime', appointTime: true, class: 'timeNowrap'},
        {name: '课程状态', value: 'reserveStatus', courseStatus: true},
        {name: '操作', value: 'btnGroup', btn: true}
      ],
      tableData: [],
      isPage: true,
      total: 0,
      tabhide: false,
      pageSize: 10,
      para: {pageNo: 1, pageSize: 10},
      tableTitle: true,
      userStr: '',
      isHide: false,
      searchList: []
    }
  },
  components: {
    Table
  },
  watch: {
    userStr: function () {
      if (this.userStr) {
        this.isHide = true
      } else {
        this.isHide = false
      }
    }
  },
  methods: {
    listSelect () {
      // 审核状态
      let option = [{
        value: 'GROUP',
        label: '团课'
      }, {
        value: 'PRIVATE',
        label: '私教课'
      }, {
        value: 'SPECIAL',
        label: '特色课'
      }]
      let option3 = []
      this.$refs.table.reloadValue('courseType', option)
    },
    getQueryParam (queryObj) {
      console.log('获取参数', queryObj)
      this.para.pageNo = 1
      for (let key in queryObj) {
        if (queryObj[key]) {
          this.para[key] = queryObj[key]
        } else {
          delete this.para[key]
        }
      }
      this.initStudent(this.para)
    },
    changeParam (page) {
      this.para.pageNo = page
      this.initStudent(this.para)
    },
    async getRowdata (data) {
      let self = this
      if (data.index === '取消预约') {
        console.log(new Date(data.row.endTime), new Date())
        if (data.row.endTime < new Date().getTime()) {
          self.$message({
            showClose: true,
            message: '该课程已结束',
            type: 'warning'
          })
          return
        }
        this.$confirm('确定取消预约该课程？', {type: 'warning'})
        .then(_ => {
          self.cancelAppoint(data.row)
        })
        .catch(_ => {})
      }
    },
    // 取消预约
    async cancelAppoint (data) {
      const res = await cancelAppoint(data.reserveCode, data.studentPid)
      if (isApiResponseOk(res)) {
        // console.log('获取数据成功', res)
        this.$message({
          showClose: true,
          message: '取消成功',
          type: 'success'
        })
        this.initStudent(this.para)
      }
    },
    // 初始化数据
    async initStudent (data) {
      const res = await courseAppointment(data)
      if (isApiResponseOk(res)) {
        if (res.data) {
          this.tableData = res.data.record
          this.total = res.data.totalNum
          for (let val of res.data.record) {
            if (val.reserveStatus == 'SUCCEED') {
              val['btnGroup'] = [{text: '取消预约'}]
            }
          }
          if (this.total === 0) {
            this.isPage = false
          } else {
            this.isPage = true
          }
        } else {
          this.isPage = false
        }
        // console.log('huoquxinxi', res)
      } else {
        this.isPage = false
      }
    },
    // 获取所有的教练
    async getallcoach () {
      const res = await getbranchcoach({branchId: ''})
      if (isApiResponseOk(res)) {
        let coachList = []
        for (var i=0; i<res.data.length; i++) {
          let obj = {}
          obj.value = res.data[i].coachId
          obj.label = res.data[i].coachName
          coachList.push(obj)
        }
        // console.log(coachList)
        this.$refs.table.reloadValue('coachId', coachList)
      }
    },
    async initbranch () {
      const res = await getallbranch()
      if (isApiResponseOk(res)) {
        let branchList = []
        for (var i=0; i<res.data.length; i++) {
          let obj = {}
          obj.value = res.data[i].id
          obj.label = res.data[i].name
          branchList.push(obj)
        }
        // console.log(branchList)
        this.$refs.table.reloadValue('branchId', branchList)
      }
    },
    // 模糊查询
    async workspaceSearch (str) {
      const res = await workspaceSearch(str)
      if (isApiResponseOk(res)) {
        if (res.data) {
          this.searchList = res.data
          if (this.searchList.length == 0) {
            this.$router.push({ name: "worklistSign", query: { str: this.userStr,personId: '', vipUser: 1 } });
          }
        }
      }
    },
    // 搜索签到
    searchSign () {
      let str = this.userStr
      if (!str) {
        return
      }
      this.workspaceSearch(str)
      // this.$router.push({ name: "worklistSign", query: { str: this.userStr } });
    },
    clearUserstr () {
      this.userStr = ''
    },
    // 搜索列表点击
    searchClick (item) {
      if (item.status == 1) {
        this.$router.push({ name: "worklistSign", query: { str: item.userName || item.email || item.phone, personId: item.personId, vipUser: item.status} });
      } else if (item.status == 2) {
        this.$router.push({ name: "worklistSign", query: { str: item.userName || item.email || item.phone, potentialVipUserId: item.potentialVipUserId, vipUser: item.status} });
      } else {
        this.$router.push({ name: "worklistSign", query: { str: item.userName || item.email || item.phone, personId: item.personId, vipUser: item.status} });
      }
    }
  },
  created () {
    // 获取学员列表
    // this.initStudent(this.para)
    this.$nextTick(_ => {
      this.initStudent(this.para).then(() => {
        this.listSelect()
        this.getallcoach()
        this.initbranch()
      })
    })
  },
}
</script>

<style>
.worklist  .table .tableHead{
  margin-top: 0;
}
.worklist .topInfo{
  margin-bottom: 0.2rem;
  width: 100%;
  display: flex;
  height: 0.36rem;
  justify-content: space-between;
  align-items: center;
}
.worklist .topInfo .searchSign{
  display: flex;
  align-items: center;
  position: relative;
}
.searchSign .icon-icon_delet{
  position: absolute;
  top: 50%;
  right: 1.36rem;
  transform: translateY(-50%);
  cursor: pointer;
  color: rgba(216,216,216,1);
}
.searchSign  .userInput{
  width: 4.4rem;
  height: 0.4rem;
  line-height: 0.34rem;
  background: rgba(232,236,238,0.30);
  border: 1px solid #E9ECEF;
  margin-right: 0.2rem;
  font-size: 0.14rem;
  color: #263238;
  padding-left: 0.12rem;
  box-sizing: border-box;
  outline: none;
}
.searchSign  .search{
  color: #20C6BA;
  border: 1px solid #20C6BA;
  border-radius: 2px;
  text-align: center;
  width: 1rem;
  height: 0.34rem;
  line-height: 0.34rem;
  font-size: 0.16rem;
  cursor: pointer;
}
.worklist .title {
  font-family: PingFangSC-Semibold;
  font-size: 0.24rem;
  color: #263238;
  letter-spacing: 0;
  line-height: 0.24rem;
  font-weight: 548;
}
.worklist .redText {
  color: red!important;
}
.worklist .table  .endCol {
  margin-left: -.9rem !important;
}
.worklist .table .tableHead .el-input--prefix .el-input__inner {
    width: 73%;
    padding: 0px;
    padding-left: .05rem;
    font-size: 0.14rem;
    color: #263238;
    letter-spacing: 0;
    line-height: 0.14rem;
}
.worklist .table .el-col div {
    font-size: .14rem;
    font-family: PingFangSC-Medium;
    color: #263238;
    line-height: .14rem;
    white-space: nowrap;
    margin-right: 0.1rem;
}
.worklist .table .el-col-4 {
    width: 2.9rem;
    padding-left: 0;
}
.worklist .table .tableHead .el-select .el-input {
    width: 100% !important;
}
.worklist .table .el-col-4:nth-child(2) {
    float: inherit;
}
.worklist .table .el-col-4:nth-child(2) div{
    margin-right: 0.14rem;
}
.worklist .table .el-col-4:nth-child(1), .worklist .table .el-col-4:nth-child(2){
    width: 2.65rem;
}
.worklist .align-right button {
  position: absolute;
  right: 0.1rem;
}
.tableTitle{
  font-family: PingFangSC-Semibold;
  font-size: 0.18rem;
  color: #8597A1;
  line-height: 0.18rem;
  margin: 0.2rem 0;
}
.worklist .table .cell{
  display: flex;
}
.worklist .table .cell .circle{
  background: #C0C4CC;
  /* display: block; */
  width: 0.06rem;
  height: 0.06rem;
  border-radius: 50%;
  margin-right: 0.2rem; 
  margin-top: .08rem;
}
.table .cell .circle.active{
  background: #74D578;
}
.table .cell .circle.success{
  background: #20C6BA;
}
.table .cell .circle.cancel{
  background: #C0C4CC;
}
.worklist .table .cell .cancle{
  margin-left: 0.2rem;
  color: #74D578;
  cursor: pointer;
}
.worklist .table .el-table__row .complete {
  opacity: 0.5;
}
.worklist .table .el-table th.is-leaf {
  height: 0.42rem;
  background: #EAEEF0;
  box-shadow: inset 0 -6px 18px 0 rgba(209,220,225,0.20);
  font-family: PingFangSC-Semibold;
  font-size: 0.12rem;
  color: #8197A3;
  line-height: 0.12rem;
}
.worklist .el-table{
  color: #263238!important;
}
.worklist .startData.el-input{
  width: 100%!important;
  transform: translateX(-0.1rem);
  margin-right: 0!important;
  margin-left: 0.1rem;
}
.worklist .endData{
  margin: 0!important;
}
.worklist .endData.el-input{
  width: 1.8rem!important;
  margin-left: 0.1rem;
}
.worklist .el-table__row{
  font-family: PingFangSC-Regular;
  font-size: 0.14rem;
  line-height: 0.14rem;
}
.worklist .timeNowrap .cell{
  white-space: nowrap;
}

.worklist .searchList,.worklist .searchList li{
  list-style: none
}
.worklist .searchList{
  position: absolute;
  top: 0.4rem;
  left: 0;
  margin: 0;
  background: #fff;
  width: 4.4rem;
  border: 1px solid #E9ECEF;
  border-top: 0;
  padding: 0;
  z-index: 11;
  max-height: 3rem;
  overflow-y: auto;
}
.worklist .searchList .searchItem{
  line-height: 0.4rem;
  padding: 0 0.1rem;
  box-sizing: border-box;
  cursor: pointer;
}
.worklist .searchList .searchItem:hover{
  background: #E9ECEF;
}

.worklist ::-webkit-scrollbar{
  width: 4px;  
  height: 16px;  
  background-color: #dcdfe6;
}
.worklist ::-webkit-track{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);  
  border-radius: 4px;  
  background-color: #fff;  
}
.worklist ::-webkit-thumb{
  border-radius: 10px;  
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);  
  background-color: #fff;
}

@media screen and (max-width: 1280px) {
  .worklist .el-select .el-input .el-select__caret{
    position: relative;
    top: -9px;
    height: 40px;
  }
  /* .worklist .table .el-col-4:nth-child(1), .worklist .table .el-col-4:nth-child(2) {
    width: 2rem;
  } */
}
@media screen and (min-width: 1281px) and (max-width: 1440px) {
  .worklist .el-select .el-input .el-select__caret{
    position: relative;
    top: -6px;
    height: 40px;
  }
  /* .worklist .table .el-col-4:nth-child(1), .worklist .table .el-col-4:nth-child(2) {
    width: 2rem;
  } */
}
@media screen and (min-width: 1440px) and (max-width: 1680px) {
  .worklist .el-select .el-input .el-select__caret{
    position: relative;
    top: -3px;
    height: 40px;
  }
}
@media screen and (min-width: 1681px) and (max-width: 1920px) {
  .worklist .el-select .el-input .el-select__caret{
    position: relative;
    top: -2px;
    height: 40px;
  }
}
</style>





